* {
  padding: 0;
  margin: 0;
  border: 0;
}
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
:focus,
:active {
  outline: none;
}
a:focus,
a:active {
  outline: none;
}
nav,
footer,
header,
aside {
  display: block;
}
html,
body {
  font-family: "Poppins";
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
  font-family: inherit;
}
input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;500;700&display=swap");
#root {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;

  flex-direction: column;
}

:root {
  --dblue: hsl(240, 38%, 20%);
  --grayish-blue: hsl(240, 18%, 77%);
}
.container {
  max-width: 1470px;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  margin: 0 auto;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}
main {
  flex: 1 1 100%;
}
body {
  font-family: Inter;
  font-size: 16px;
  color: var(--dblue);
  @media (max-width: 375px) {
    font-size: 18px;
  }

  font-weight: 500;
}
.header {
  width: 100%;
  height: 100%;
  padding-top: 2rem;
  padding-bottom: 2rem;
  position: relative;
}
.header-container {
  position: relative;
  width: 100%;
  height: 100%;
  border-bottom: solid 1px hsl(240, 18%, 77%);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-logo {
  padding-right: 4rem;
  @media (max-width: 767.98px) {
    padding-right: 6rem;
  }
  @media (max-width: 523.98px) {
    padding-right: 2rem;
  }
}
.header-body {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}
.burger {
  position: absolute;
  left: -100%;
  height: 2000px;

  opacity: 0;
  top: -28px;
  background-color: #fff;
  max-width: 60%;
  flex-direction: column-reverse;
  justify-content: flex-start;

  align-items: start;
  transition: all 0.3s;
  z-index: 8;
  &.active {
    left: 0;
    overflow: hidden;
    opacity: 1;
  }
}

.user-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.filter {
  width: 2000px;
  height: 2000px;
  top: -100px;
  left: 0;
  position: absolute;
  z-index: 7;
  background-color: rgba(0, 0, 0, 0.707);
}
.header-nav {
  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;

  & .header-list {
    display: flex;
    align-items: center;
    justify-content: space-between;

    width: 100%;
    height: 100%;

    @media (max-width: 767.98px) {
      padding-top: 100px;
      justify-content: flex-start;
      flex-direction: column;
      align-items: start;
      padding-left: 20px;
      height: 100%;

      gap: 32px;
      max-width: 250px;
    }
    max-width: 500px;
    & .header-li {
      padding-right: 1rem;

      & .header-link {
        @media (min-width: 767.98px) {
          padding-bottom: 40px;
          border-bottom: solid 1px hsl(220, 14%, 75%);

          &:hover {
            padding-bottom: 37px;
            border-bottom: solid 4px hsl(26, 100%, 55%);
          }
        }
        color: hsl(219, 9%, 45%);
        transition: all 0.3s;
        &:hover {
          color: hsl(220, 13%, 13%);
        }
        @media (max-width: 767.98px) {
          &:hover {
            border-bottom: solid 3px hsl(26, 100%, 55%);
          }
        }
      }
    }
  }
}

.header-cart {
  font-size: 16px;
  position: relative;
  cursor: pointer;
  & span {
    color: #fff;
    background-color: hsl(26, 100%, 55%);
    position: absolute;
    font-size: 18px;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 15px;
    top: -10px;
    right: -5px;
    @media (max-width: 523.98px) {
      font-size: 16px;
      width: 18px;
      height: 20px;
    }
  }
  & svg {
    fill: hsl(219, 9%, 45%);
    width: 40px;
    height: 40px;
    transition: all 0.3s;
    &:hover {
      fill: hsl(220, 13%, 13%);
    }
    @media (max-width: 523.98px) {
      width: 25px;
      height: 25px;
    }
  }
}
.user-avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  padding-bottom: 94px;
  position: relative;
  border: solid 3px #fff;
  transition: all 0.3s;
  cursor: pointer;
  @media (max-width: 767.98px) {
    width: 70px;
    height: 70px;
    padding-bottom: 64px;
  }
  @media (max-width: 523.98px) {
    width: 50px;
    height: 50px;
    padding-bottom: 44px;
  }
  &:hover {
    border: solid 3px hsl(26, 100%, 55%);
  }
  & img {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    object-fit: cover;
  }
}
.page {
  width: 100%;
  height: 100%;
  display: flex;
}
.filter-img {
  width: 100%;
  height: 100%;
  top: 0;

  left: 0;
  position: absolute;
  z-index: 1;
  background-color: hsla(230, 60%, 98%, 0.773);
  border: solid 5px hsl(26, 100%, 55%);
  border-radius: 15px;
}
.page-container {
  display: flex;
  width: 100%;
  height: 100%;
  @media (max-width: 767.98px) {
    flex-direction: column;
  }
}

.leftpart_body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  display: flex;
  flex: 1 1 50%;
  padding: 5rem;
  @media (max-width: 1099px) {
    padding: 2rem;
  }
}
.looped-image {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
  padding-bottom: 100%;
  & img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
  }
}
.item-images {
  margin-top: 4.5rem;
  width: 100%;
  height: 100%;
  display: flex;
  gap: 3rem;
}

.thumb-image {
  width: 100%;
  height: 100%;
  position: relative;
  padding-bottom: 20%;
  border-radius: 15px;
  overflow: hidden;

  & img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
  }
}

.rightPart-body {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  flex: 1 0 50%;
  padding: 5rem;
  @media (max-width: 1099px) {
    padding: 2rem;
  }
  @media (max-width: 967.98px) {
    padding: 0 2rem;
  }
}
.brand-name {
  text-transform: uppercase;
  color: hsl(26, 100%, 55%);
  margin-bottom: 1rem;

  font-size: 20px;
  font-weight: 700;
  line-height: 120%;
  @media (max-width: 967.98px) {
    font-size: 18px;
    margin-bottom: 0.5rem;
  }
}
.sneakers-name {
  color: hsl(240, 38%, 20%);
  font-size: 48px;
  font-weight: 700;

  line-height: 135%;
  margin-bottom: 2rem;
  @media (max-width: 967.98px) {
    font-size: 40px;
    margin-bottom: 1.5rem;
  }
}
.sneakers-describe {
  color: hsl(219, 9%, 45%);
  font-weight: 500;
  line-height: 170%;
  margin-bottom: 2rem;
  @media (max-width: 967.98px) {
    margin-bottom: 1.5rem;
  }
}
.prices {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.price {
  color: hsl(240, 38%, 20%);
  font-weight: 700;
  font-size: 22px;
  @media (max-width: 967.98px) {
    font-size: 18px;
  }

  & span {
    margin-left: 1rem;
    padding: 0.3rem 0.4rem;
    color: hsl(26, 100%, 55%);
    background-color: hsl(25, 100%, 94%);
    text-align: center;
  }
}
.old_price {
  color: hsl(220, 14%, 75%);
  font-weight: 700;
  font-size: 18px;
  text-decoration: line-through;
  margin-bottom: 1rem;
  @media (max-width: 967.98px) {
    margin-bottom: 0.5rem;
  }
}
.buttons {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}
.change_quantity {
  display: flex;
  align-items: center;
  & span {
    padding: 1.2rem 1.8rem;
  }
  border-radius: 10px;
  overflow: hidden;
  background: hsl(223, 64%, 98%);
}
.decriment {
  padding: 0.8rem 1.2rem;
  color: hsl(26, 100%, 55%);
  background: hsl(223, 64%, 98%);
  font-size: 22px;
  @media (max-width: 967.98px) {
    padding: 0.6rem 1rem;
  }
}
.increment {
  padding: 0.8rem 1.2rem;
  font-size: 22px;
  background: hsl(223, 64%, 98%);
  color: hsl(26, 100%, 55%);
  @media (max-width: 967.98px) {
    padding: 0.6rem 1rem;
  }
}
.toCart {
  background-color: hsl(26, 100%, 55%);
  padding: 1.2rem 4rem;
  transition: all 0.3s;
  @media (max-width: 967.98px) {
    padding: 0.8rem 3.5rem;
  }

  border-radius: 10px;
  color: #fff;
  display: flex;
  align-items: center;
  &:hover {
    background-color: hsla(26, 100%, 55%, 0.538);
  }
  & svg {
    margin-right: 0.8rem;
  }
}
.slider-body {
  position: relative;
}
.slider-item {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 100;
  border-radius: 25px;
}
.slider-bg {
}
.slider-bg::before {
  content: "";
  pointer-events: none;
  background-color: hsla(220, 19%, 6%, 0.526);
  width: 300%;
  height: 300%;
  top: -150%;
  left: -150%;

  position: absolute;
  z-index: -1;
}
.slider-close-btn {
  width: 50px;
  height: 50px;
  position: absolute;
  color: #fff;
  right: -10px;
  top: -80px;
}
.alice-carousel__prev-btn {
  position: absolute;
  width: 100px !important;
  height: 100px;
  top: 40%;
  left: -100px;
  z-index: 4;
  color: #fff;
  display: flex !important;
  align-items: center;
  justify-content: center;
}
.alice-carousel__prev-btn-wrapper {
  opacity: 1 !important;
  border-radius: 50% !important;
  overflow: hidden;
  display: flex !important;
  width: 70px;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  height: 70px;
  .alice-carousel__prev-btn-item span {
    color: #000;
    font-size: 50px;

    opacity: 1;
    z-index: 4;
  }
}
.alice-carousel__next-btn {
  position: absolute;
  width: 100px !important;
  height: 100px;
  top: 40%;
  right: -100px;
  z-index: 4;
  color: #fff;
  display: flex !important;
  align-items: center;
  justify-content: center;
}
.alice-carousel__next-btn-wrapper {
  opacity: 1 !important;
  border-radius: 50% !important;
  overflow: hidden;
  display: flex !important;
  width: 70px;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  height: 70px;
  .alice-carousel__next-btn-item span {
    color: #000;
    font-size: 50px;

    opacity: 1;
    z-index: 4;
  }
}
.alice-carousel__dots {
  display: none !important;
}
.slider-item-mobile {
  width: 100%;
  height: 100%;
}
.slider-wrap-mobile {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  margin-bottom: 2rem;
}
.slider-body-mobile {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  .alice-carousel {
    .alice-carousel__prev-btn {
      position: absolute;
      width: 100px !important;
      height: 100px;
      top: 40%;
      left: 0;
      z-index: 4;
      color: #fff;
      display: flex !important;
      align-items: center;
      justify-content: center;
    }
    .alice-carousel__next-btn {
      position: absolute;
      width: 100px !important;
      height: 100px;
      top: 40%;
      right: 0;
      z-index: 5;
      color: #fff;
      display: flex !important;
      align-items: center;
      justify-content: center;
    }
  }
}
.full-cart {
  position: absolute;
  top: 180px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  right: 50px;
  width: 40%;
  min-height: 300px;
  padding: 1rem;
  border-radius: 15px;
  background-color: #fff;
  box-shadow: 2px 5px 3px hsla(0, 0%, 0%, 0.523);
  border-bottom: solid 1px hsl(240, 38%, 20%);
  border-left: solid 1px hsl(240, 38%, 20%);
  border-right: solid 1px hsl(240, 38%, 20%);
  z-index: 6;
  @media (max-width: 767.98px) {
    width: 91%;
    height: 350px;
    right: 30px;
    top: 110px;
  }
}
.cart-name {
  margin-bottom: 16px;
}
.cartDescribe {
}
.cart-list {
  width: 100%;

  height: 100%;

  display: flex;
}
.cart-item {
  flex: 1 1 100%;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  max-height: 80px;
  gap: 0.5rem;
  & img {
    width: 100%;
    max-width: 80px;
    max-height: 80px;
    border-radius: 15px;
    height: 100%;
    object-fit: contain;
  }
  & h3 {
    max-width: 30%;
  }
  & p {
  }
  & a {
    & svg {
      font-size: 20px;
      width: 25px;
      height: 25;
    }
  }
}
.cart-checkout {
  position: absolute;
  bottom: 5%;
  left: 5%;
  width: 90%;
  text-align: center;
  padding: 1.2rem 4rem;
  border-radius: 15px;
  color: #fff;
  background-color: hsl(26, 100%, 55%);
}
